@import "main";


html {
    font-size: 100%
  }

  body {
    font-family: 'Lora', 'Times New Roman', serif;
    font-size: 1.125rem;
    color: #404040;
    position: relative;
    background-color: #FFFFFF;
    
    overflow-wrap: break-word;
  }
  p {
    line-height: 1.5;
    margin: 1.875rem 0;
  }
  h1,h2,h3,h4,h5,h6 {
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 800;
    line-height: 1.1;
  }
  h1 {
    font-size: 2.25rem;
  }
  h2 {
    font-size: 1.875rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  h4 {
    font-size: 1.125rem;
  }
  h1, h2, h3, h4 {
    margin-top: 1.25rem;
  }
  a {
    color: #008AFF;
  }
  a:hover,
  a:focus {
    color: #0085A1;
  }
  blockquote {
    color: #808080;
    font-style: italic;
  }
  blockquote p:first-child {
    margin-top: 0;
  }
  hr.small {
    max-width: 6.25rem;
    margin: 1rem auto;
    border-width: 0.25rem;
    border-color: inherit;
    border-radius: 0.1875rem;
  }
  nav {
    display: block;
    padding: 8px;
    background-color: #dddddd;

  }

  nav a {
    display: inline;
    padding: 8px;
    background-color: #d8d49d;

  }